<template>
  <el-container>
    <el-header>
      <div>
        <el-button type="primary" @click="startHacking('Training')">训练数据</el-button>
        <el-button type="primary" @click="startHacking('Content')">新问题</el-button>
      </div>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <Tree></Tree>
      </el-aside>
      <el-main>
        <Content v-if="mainType == 'Content'"></Content>
        <Training v-if="mainType == 'Training'"></Training>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import Tree from './pages/tree.vue';
import Content from './pages/content.vue';
import Training from './pages/training.vue';


export default {
  components: {
    Tree, // 注册树形结构组件,
    Content, // 注册内容组件
    Training, // 注册训练组件
  },
  data() {
    return {
      mainType: "Content",
    }
  },
  methods: {
    startHacking(type) {
      this.mainType = type;
    }
  }
}
</script>

<style>
.el-header,
.el-footer {
  background-color: #B3C0D1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #D3DCE6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  background-color: #E9EEF3;
  color: #333;
  text-align: center;
}

body>.el-container {
  margin-bottom: 40px;
}

</style>



