<template>
  <div>
    <el-carousel :interval="4000" type="card" height="100px">
      <el-carousel-item v-for="item in question_list" :key="item">
        <el-button type="text" @click="quiteQuestion(item)" :disabled=buttonEnabled>
          <h4 style="font-weight: bold; font-size: 18px;color: black;">{{ item }}</h4>
        </el-button>
      </el-carousel-item>
    </el-carousel>
    <el-row>
      <el-col :span="20"><el-input v-model="question" type="textarea" rows="3" placeholder="请输入内容"></el-input></el-col>
      <el-col :span="2"><el-button :disabled=buttonEnabled type="primary" @click="postQuestion">提问</el-button></el-col>
    </el-row>
    <el-row>
      <el-col :span="20"><el-input v-model="sql" type="textarea" rows="5" placeholder="请输入内容"></el-input></el-col>
      <el-col :span="2"><el-button :disabled=buttonEnabled type="primary" @click="postData">查询数据</el-button></el-col>
      <el-col :span="2"><el-button :disabled=buttonEnabled type="primary" @click="postTraining"
          icon="el-icon-success"></el-button></el-col>
    </el-row>
    <el-row>
      <el-table :data="tableData" stripe style="width: 100%">
        <!-- 使用 v-for 遍历动态列 -->
        <el-table-column v-for="(value, key) in tableData[0]" :key="key" :prop="key" :label="key"></el-table-column>
      </el-table>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      options: [],
      id: '5e9e71d9-fab9-49a4-9cf4-dc9dcb7686a3',
      question: "查询前10只基金",
      sql: "",
      buttonEnabled: false,
      tableData: [],
      question_list: [],
    }
  },
  async created() {
    this.id = this.$cookie.get('id');
    this.sql = this.$cookie.get('sql');
    this.question = this.$cookie.get('question');
    try {
      const response = await axios.get('/api/v0/generate_questions');
      this.question_list = response.data.questions;
    } catch (error) {
      console.error(error);
    };
  },
  methods: {
    quiteQuestion(question) {
      this.question = question;
    },

    async postData() {
      try {
        this.buttonEnabled = true;
        const response = await axios.post('/api/v0/run_sql?id=' + this.id, {
          sql: this.sql
        });
        if (response.data.error == undefined) {
          this.tableData = JSON.parse(response.data.df);
        }
        else {
          this.$message({
            message: '错误信息:' + response.data.error,
            type: 'error',
            duration: 10000
          });
        }
      } catch (error) {
        console.error(error);
      };
      this.buttonEnabled = false
    },
    async postQuestion() {
      try {
        this.buttonEnabled = true;
        const response = await axios.post('/api/v0/generate_sql', {
          question: this.question
        });
        this.sql = response.data.text;
        this.id = response.data.id;
        this.tableData = JSON.parse(response.data.df);
        this.$cookie.set('sql', this.sql, 7);
        this.$cookie.set('id', this.id, 7);
        this.$cookie.set('question', this.question, 7);
      } catch (error) {
        console.error(error);
      };
      this.buttonEnabled = false
    },
    async postTraining() {
      try {
        const response = await axios.post('/api/v0/train', {
          question: this.question,
          training_data_type: "sql",
          content: this.sql,
        });
        this.$message({
          message: '感谢您提供了一条成功案例！',
          type: 'success'
        });
      } catch (error) {
        console.error(error);
      };
    },
  }
}
</script>
<style>
.el-row {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.el-col {
  border-radius: 4px;
}

.bg-purple-dark {
  background: #99a9bf;
}

.bg-purple {
  background: #d3dce6;
}

.bg-purple-light {
  background: #e5e9f2;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}

.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}

.button-container {
  display: flex;
  align-items: flex-end;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 100px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}
</style>


