<template>
  <div>
    <el-row>
      <el-button type="text" @click="dialogFormVisible = true">添加训练数据</el-button>
      <el-dialog title="添加训练数据" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="类型选择" :label-width="formLabelWidth">
            <el-radio v-model="form.training_data_type" label="ddl">DDL</el-radio>
            <el-radio v-model="form.training_data_type" label="documentation">文档</el-radio>
            <el-radio v-model="form.training_data_type" label="sql">问题-SQL对</el-radio>
          </el-form-item>
          <el-form-item label="问题" :label-width="formLabelWidth">
            <el-input v-model="form.question" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="SQL" :label-width="formLabelWidth">
            <el-input v-model="form.content" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="postTraining">确 定</el-button>
        </div>
      </el-dialog>
    </el-row>
    <el-row>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="question" label="问题">
        </el-table-column>
        <el-table-column prop="content" label="答案\DDL\SQL">
        </el-table-column>
        <el-table-column prop="training_data_type" label="训练数据类型" width="180">
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="120">
          <template slot-scope="scope">
            <el-button @click="deleteRow(scope.row.id)" type="text" size="small">
              移除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      tableData: [],
      dialogFormVisible: false,
      form: {
        training_data_type: 'ddl',
        content: '',
        question: "",
      },
      formLabelWidth: '120px'
    }
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        const response = await axios.get('/api/v0/get_training_data');
        this.tableData = JSON.parse(response.data.df);
      } catch (error) {
        console.error(error);
      };
    },
    async deleteRow(id) {
      try {
        const response = await axios.post('/api/v0/remove_training_data',{
          id: id,
        });
        // 删除成功后重新获取数据
        this.fetchData();
      } catch (error) {
        console.error(error);
      };
    },
    async postTraining() {
      try {
        const response = await axios.post('/api/v0/train', {
          question: this.form.question,
          training_data_type:this.form.training_data_type,
          content: this.form.content,
        });
        // 关闭对话框
        this.dialogFormVisible = false;
        // 添加数据成功后重新获取数据
        this.fetchData();
      } catch (error) {
        console.error(error);
      };
    },
  }
}
</script>

<style>
.el-row {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.el-col {
  border-radius: 4px;
}

.bg-purple-dark {
  background: #99a9bf;
}

.bg-purple {
  background: #d3dce6;
}

.bg-purple-light {
  background: #e5e9f2;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}

.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}

.button-container {
  display: flex;
  align-items: flex-end;
}
</style>


