<template>
  <div>
    <el-table
      :data="question_history"
      style="width: 100%">
      <el-table-column
        prop="question">
      </el-table-column>
    </el-table>    
    <el-select v-model="value" placeholder="请选择">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
      </el-option>
    </el-select>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
      return {
        options: [],
        value: '',
        question_history: []
      }
  },
  async created() {
    try {
      const response = await axios.get('/api/v0/get_question_history');
      this.question_history = response.data.questions;
    } catch (error) {
      console.error(error);
    };
  },
  methods: {
    async postData() {
      try {
        const response = await axios.post('http://example.com/api/post', {
          data: 'your data'
        });
        console.log(response.data);
      } catch (error) {
        console.error(error);
      }
    },
  }
}
</script>
<style>
.radio-container {
  /* 单选框单倍间距 */
  display: flex;
  align-items: left;
  justify-content: space-between;
  margin: 0 10px;
}
</style>


